.tall {
  min-height: 70vh;
  box-shadow: none;
}

.advanced {
  font-size: 14px;
  padding: 0 0 16px 16px;
}

.category {
  border: 1px solid rgb(125, 125, 125);
  border-width: 0px 0 1px 0;
}
.margined {
  margin: 0 0.5em;
}

h6.margined  {
  font-size: 1.15rem;
  font-weight: 600;
}

.spaced > div {
  font-size: 0.8em;
}

.trash {
  position: absolute;
  padding: 5px;
  margin-top: 0.3em;
  margin-left: 0.6em;
}

.grey {
  color: grey;
}

.unpadded {
  padding: 5px;
  margin-right: 0.3em;
}

:invalid {
  border: tomato solid 3px;
}

.outlined {
  border: solid 2px;
  border-radius: 10em;
  margin-left: 0;
  padding-right: 0.3em;
}

.accordion{
  box-shadow: none;

}

.summary {
  cursor: pointer;
  min-height: 0;
}

.summary > div {
  margin: 10px 0;
}

.section > div .accordion:first-child > div > div{
  margin: 0 0 10px 0;
}

.section {
  padding: 0 16px 0;
}

.aligned {
  display: flex;
  align-items: center;
  margin: 0.5em;
}

.button {
  text-align: right;
  margin-top: 1em;
}

.search {
  margin-right: 1em;
}

.accordion div:has(.aligned){
  padding:0;
}